import {mapState,mapMutations} from 'vuex'
import swiperImage from "@/components/index/swiper-image.vue"
import baseInfo from "@/components/detail/base-info.vue"
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue'
import commentsScroll from '@/components/detail/comments-scroll.vue';
import bottomBtn from "@/components/detail/bottom-btn.vue"
import uParse from "@/components/uni-ui/uParse/src/wxParse.vue"
import wacradioGroup from '@/components/common/radio-group.vue'
import uniNumberBox from '@/components/uni-ui/uni-number-box/uni-number-box.vue'

export default {
	components:{
		swiperImage,baseInfo,uniListItem,commentsScroll,uParse,bottomBtn,
		wacradioGroup,uniNumberBox
	},
	data() {
		return {
			comments:[],
			banners:[],
			detail:{},
			content:""
		}
	},
	computed:{
		...mapState({
			pathList:state=>state.path.list,
			userInfo:state=>state.user.userInfo
		})
	},
	// 监听页面返回事件
	onBackPress() {
		//关闭模态框
		for (let key in this.popup) {
			if(this.popup[key] !== 'none') {
				this.hide(key)
				return true
			}
		}
	},
	onLoad(e) {
		//console.log(e.id);
		if(e.id){
			this.__init(e.id)
		}
		
	},
	methods: {
		...mapMutations([
			'addGoodsToCart'
		]),
		
		async __init(id){
			console.log(id)
			await this.$H.get('/product/product/get/'+id).then(res=>{
				console.log(res);
				let product = res.detail
				uni.setNavigationBarTitle({
					title:product.title
				})
				
				this.banners = product.imagesObject
				
				this.detail = {
					id:product.id,
					title:product.title,
					cover:product.cover,
					desci:product.desci,
					price:product.price,
					num:product.num
				}
				this.content = product.content
				
				this.comments = res.comments.map(v=>{
					return {
						userpic:v.user.avatar,
						username:v.user.username,
						createTime:v.review.updateTime,
						context:v.review.detail,
						imgList:v.review.imageObject
					}
				})
				console.log(res.comments)
			})
			
		},
		//加入购物车
		addCart(){
			this.$H.post('/product/cart/add',{
				userId:this.userInfo.id,
				productId:this.detail.id
			},{
				token:true
			}).then(res=>{
				console.log(res)
				// 通知购物车页面更新数据
				uni.$emit('updateCart')
				uni.showToast({
					title:res
				})
			}).catch(err=>{
				
			})
		},
		preview(){
			
		},
		navigate(href, e) {
			console.log("href:" +href);
			uni.showModal({
				content:"点击链接为："+href,
				showCancel:false
			})
		}
	}
}
